
/********************** Navigation  *************************/
.nav {
  display: flex;
  justify-content: space-around;
  background-color: rgb(212, 83, 125);
  height: 80px;
}
a {
  text-decoration: none;
}
.nav a {
  text-decoration: none;
  color: white;
  margin: auto;
  position: relative;
}
/******************* Nav Hover Animation ******************/
/* _ _ _ _ _ _ _ _ _ _ _ _ _ _ _ _ _ _ _ _ _ _ _ _ _ _ _ _ _
 (_)(_)(_(_(_(_(_(_(_(_(_(_(_(_(_(_(_(_(_(_(_(_(_(_(_(_(_(_(_) */


.nav a:hover {
  color: #f8e6fb;
}
.nav a:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  bottom: 0;
  left: 0;
  background-color: #d49e6c;
  visibility: hidden;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}
.nav a:hover:before {
  visibility: visible;
  -webkit-transform: scaleX(1.5);
  transform: scaleX(1.5);
}

